@media(min-width: 1200px) {
    .wrapper {
        display: grid;
        grid-template-columns: minmax(0, 1fr) minmax(0, 1fr);
        gap: 2%;
    }
    .wrapper>div {
        padding: 0.2em;
        text-align: center;
    }
    .boxTrue>a {
        color: #009c15;
    }
    .boxFalse>a {
        color: #b10000;
    }
}

@media(max-width: 1200px) and (min-width: 650px) {
    .wrapper {
        display: grid;
        grid-template-columns: minmax(0, 1fr) minmax(0, 1fr);
        gap: 2%;
    }
    .wrapper>div {
        padding: 0.2em;
        text-align: center;
    }
    .boxTrue>a {
        color: #009c15;
    }
    .boxFalse>a {
        color: #b10000;
    }
}

@media(max-width: 650px) {
    .wrapper {
        display: grid;
        width: 100%;
    }
    .wrapper>div {
        padding: 0.2em;
        text-align: center;
    }
    .boxFalse {
        grid-row-start: 3;
        grid-row-end: 4;
    }
    .boxTrue>a {
        color: #009c15;
    }
    .boxFalse>a {
        color: #b10000;
    }
}