.all {
    float: left;
    width: 100%;
    padding: 0.2%;
    background-color: #6da397;
    color: #ffffff;
    border-bottom: #3a6158 3px solid;
}

.all .wrappSpan {
    float: left;
    width: 5%;
}

.all .wrappSpan span {
    cursor: pointer;
    font-size: 2rem;
    line-height: 2.3rem;
    color: #100917;
}

.click_1::before {
    content: "\2630";
}

.click_2::before {
    content: "\2A09";
}

.openMenu {
    float: left;
    padding: 0.15em;
    font-size: 1.4rem;
    line-height: 1.6rem;
}

.openMenu a, a:hover {
    margin-left: 0.3em;
}

.openMenu a {
    color: #241738;
}

.openMenu a:hover {
    text-decoration: none;
    color: #a10073;
}

.closedMenu {
    display: none;
}