@media(min-width: 1200px) {
    .wrapper {
        display: grid;
        grid-template-columns: minmax(0, 1fr) minmax(0, 1fr);
        gap: 2%;
        padding: 2em;
    }
    .wrapper>div {
        padding: 1em;
        border-radius: 0.5em;
        background-color: #f2f8f9;
        border: 2px solid silver;
    }
    .box {
        /*grid-column: 1 / 3;*/
        grid-column-start: 1;
        grid-column-end: 3;
        grid-row-start: 2;
        grid-row-end: 3;
    }
}
@media(max-width: 1200px) and (min-width: 770px){
    .wrapper {
        display: grid;
        grid-template-columns: minmax(0, 1fr) minmax(0, 1fr);
        gap: 1%;
        padding: 0.5em;
    }
    .wrapper>div {
        padding: 1em;
        border-radius: 0.5em;
        background-color: #f2f8f9;
        border: 2px solid silver;
    }
    .box {
        /*grid-column: 1 / 3;*/
        grid-column-start: 1;
        grid-column-end: 3;
        grid-row-start: 2;
        grid-row-end: 3;
    }
}
@media(max-width: 770px){
    .wrapper {
        padding: 0.5em;
    }
    .wrapper > div {
        padding: 1em;
        margin-bottom: 1em;
        border-radius: 0.5em;
        background-color: #f2f8f9;
        border: 2px solid silver;
    }
    .box > div {
        border-bottom: 2px solid silver;
    }
}