* { 
    margin: 0;
    padding: 0;
    outline: none;
}

* {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

html {
    height: 100%
}

body {
    width: 100%;
    height: 100%;
    float: left;
    background-color: /*#f4fcf5*/ #f2f9f3;
    color: #1a1e1b;
    font-family: sans-serif, Arial, Helvetica;
    font-size: 1rem;
    line-height: 1.5rem;
}

a {
    text-decoration: none;
    color: #1a1e1b;
}

a:hover {
    color: #008c0e;
    text-decoration: underline;
}

a, a:hover {
    -webkit-transition: all.2s ease;
    -moz-transition: all.2s ease;
    -o-transition: all.2s ease;
    -ms-transition: all.2s ease;
    transition: all.2s ease;
}

.clear {
    clear: both;
}

h1 {
    text-align: center;
    font-size: 1.5rem;
    line-height: 1.8rem;
    color: #6d506e;
}

h2 {
    font-size: 1.4rem;
    line-height: 1.7rem;
    color: #077512;
}

h3 {
    font-size: 1.3rem;
    line-height: 1.6rem;
    color: #077512;
}

h4 {
    font-size: 1.2rem;
    line-height: 1.5rem;
    color: #077512;
}

h5 {
    font-size: 1.1rem;
    line-height: 1.4rem;
    color: #008c0e;
}

p {
    padding-left: 2%;
    margin-top: 1rem;
    font-size: 1.1rem;
    line-height: 1.5rem;
}