@media(min-width: 1200px) {
    .wrapper {
        box-sizing: border-box;
        min-height: 100%;
        height: 100%;
        padding-left: 10em;
        padding-right: 10em;
    }
    .wrapper>p {
        border-left: 3px solid #3a6158;
        border-right: 3px solid #3a6158;
    }
}
@media(max-width: 1200px) and (min-width: 700px){
    .wrapper {
        box-sizing: border-box;
        min-height: 100%;
        height: 100%;
        padding-left: 7em;
        padding-right: 7em;
    }
    .wrapper>p {
        border-left: 3px solid #3a6158;
        border-right: 3px solid #3a6158;
    }
}
@media(max-width: 700px){
    .wrapper {
        box-sizing: border-box;
        min-height: 100%;
        height: 100%;
        padding-left: 1em;
        padding-right: 1em;
    }
    .wrapper>p {
        border-left: 3px solid #3a6158;
        border-right: 3px solid #3a6158;
    }
}