.eTerminal { 
  display: grid;
  grid-template-rows: minmax(0, 1fr) minmax(0, 1fr);
  grid-template-columns: minmax(0, 1fr) minmax(0, 1fr);
  color: cornsilk;
  background-color: #1c1c75;
  font-size: 0.9rem;
  line-height: 1.3rem;
}

.eTerminal>div {
  display: grid;
  grid-template-columns: minmax(0, 1fr) minmax(0, 9fr);
  grid-template-areas: "div div";
  border: 1px #ffffff dashed;
  padding: 0.3em;
  margin: 0.3em;
  word-wrap: break-word;
  overflow-wrap: break-word;
}

.spanMod {
  -webkit-animation: eTerminal 2s linear infinite;
  animation: eTerminal 2s linear infinite;
}

.bag{
  background-color: red;
}

@-webkit-keyframes eTerminal {
  50% {
    color: rgb(34, 34, 34);
  }
  51% {
    color: rgba(34, 34, 34, 0);
  }
  100% {
    color: rgba(34, 34, 34, 0);
  }
}

@keyframes eTerminal {
  50% {
    color: rgb(34, 34, 34);
  }
  51% {
    color: rgba(34, 34, 34, 0);
  }
  100% {
    color: rgba(34, 34, 34, 0);
  }
}


textarea {
  width: 100%;
  resize: vertical;
}

textarea:focus {
  outline: none !important;
  border: 2px solid #6da397;
}

.textDiv {
  color: #c95c47;
  border-bottom: 2px solid #c95c47;
}
button {
  margin: 0.5em;
  background: -moz-linear-gradient(#9aeddb, #80c4b5, #9aeddb);
  background: -webkit-gradient(linear, 0 0, 0  100%, from(#9aeddb), to(#9aeddb), color-stop(0.5, #80c4b5));
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#00BBD6', endColorstr='#EBFFFF');
  padding: 3px 7px;
  color: #333;
  -moz-border-radius: 5px;
  -webkit-border-radius: 5px;
  border-radius: 5px;
  border: 1px solid rgb(216, 201, 201);
 }

 button:active {
  margin: 0.5em;
  background: -moz-linear-gradient(#80c4b5,#9aeddb, #80c4b5 );
  background: -webkit-gradient(linear, 0 0, 0  100%, from(#80c4b5), to(#80c4b5), color-stop(0.5, #9aeddb));
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#00BBD6', endColorstr='#EBFFFF');
  padding: 3px 7px;
  color: #3a6158;
  -moz-border-radius: 5px;
  -webkit-border-radius: 5px;
  border-radius: 5px;
  border: 1px solid #3a6158;
 }